@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;700;900&display=swap);
*{margin:0;padding:0}html{background-color:#003300;background-image:url(/weather/static/media/temple.409b1c5b.svg);font-family:'Montserrat', sans-serif}.App{margin:0 auto;height:100%;width:100%;text-align:center;color:white}.title{margin:0 auto;padding:0.2rem 1rem;width:60%;display:flex;flex-direction:row;justify-content:space-around;border-radius:6px;color:white;background-color:rgba(128,128,128,0.5)}.weather{height:100px;width:60%;margin:0 auto;margin-top:1rem;padding:1rem;line-height:2rem;font-size:1.5rem;display:flex;justify-content:space-around;border-radius:6px;color:white;background-color:rgba(128,128,128,0.5)}.weather ul{display:flex;flex:1 1;flex-direction:column;justify-content:space-between}.weather li{list-style-type:none;text-align:left}@media screen and (max-width: 700px){.title{font-size:.6rem;line-height:1rem;width:80%;padding:1rem;display:flex;justify-content:space-around;flex-direction:row;margin:0 auto;color:white;background-color:rgba(128,128,128,0.5);border-radius:6px}.weather{line-height:2.5rem;padding:1rem;font-size:14px;display:flex;justify-content:space-around;border-radius:6px;margin:0 auto;margin-top:1rem;height:20%;width:80%;color:white;background-color:rgba(128,128,128,0.5)}.weather ul{display:flex;flex:1 1;flex-direction:column;justify-content:space-between}.weather li{list-style-type:none;text-align:left}}

